body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.App {
  display: flex;
}

.flats {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.main {
  display: flex;
  flex-basis: 60%;
  flex-direction: column;
}

.search {
  padding: 10px;
  margin-right: 5px;
}

.search input {
  color: #999;
  font-size: 24px;
  font-weight: 400;
  height: 40px;
  line-height: 24px;
  padding-left: 5px;
  width: 100%;
}

.map{
  flex-grow: 1;
  height: 100vh;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
}
.flat {
  flex-basis: calc(50% - 20px);
  margin: 10px;
  cursor: pointer;
}

.flat:hover {
  opacity: 0.8;
}

.flat-picture{
  background-position: center;
  background-size: cover;
  height: 200px;
}

.flat-title {
  font-weight: 600;
  margin-top: 10px;
}

.marker {
  background-color: white;
  border: 1px solid #999;
  border-radius: 7px;
  font-size: 18px;
  padding: 4px 0;
  text-align: center;
  width: 48px;
}

.marker.selected {
  background-color: yellow;
  border: black;
}

